header {
	margin: 12px;
	font-size: 28px;
	line-height: 0px;
	height:28px;
}

.custom-file-input:hover {
	cursor: pointer;
	opacity: 0;
}

.custom-file {
	opacity: 0.7;
}

#drag_drop_tag {
	margin-top: 25px;
}

input[type=number] {
	width: 6.5em;
}

#drop_zone {
	border-width: 2px;
	border-radius: 5px;
	border-bottom-left-radius: 15px;
	border-bottom-right-radius: 15px;
	width:  100%;
	height: 200px;
	max-height: 400px;
}

#user_loaded_image {
	width:  100%;
	max-height: 400px;
	padding-bottom: 15px;
}

.App {
	max-width: 500px;
}

.settingsToggle {
	cursor:pointer;
}

.settingsToggle:hover {
	color: white;
	background-color: #007bff;
}